import React, { ReactElement } from "react";
import * as AttendanceEntityAPI from "../api/attendance-entities";
import {
  AttendanceEntity,
  AttendanceEntityDTO,
} from "../api/attendance-entities/model.interface";
import { Grid } from "@material-ui/core";
import withPageLayout from "../hocs/withPageLayout";
import { PageHeaderContext } from "../context/layout";
import AppBtn from "../components/form/AppBtn";
import AppInput from "../components/form/AppInput";
import { withRouter } from "react-router";

const AttendanceEntityAdd = (props: any) => {
  const LayoutContext = React.useContext(PageHeaderContext);

  const [name, setName] = React.useState("");
  const [isRequired, setIsRequired] = React.useState(0);

  const addAttendanceEntity = async () => {
    props.setShowLoading(true);

    const attendanceEntity: AttendanceEntityDTO = {
      name,
      is_required: isRequired,
      created_by: 1,
      updated_by: 1,
    };
    const addedRole = await AttendanceEntityAPI.post(attendanceEntity);
    props.setShowLoading(false);

    typeof props.history.goBack === "function" && props.history.goBack();
  };

  React.useEffect(() => {
    LayoutContext.setTitle("Add Type");
  }, []);

  return (
    <>
      <Grid>
        <AppInput
          label="Name"
          value={name}
          placeholder="Enter Name"
          prefix={null}
          suffix={null}
          onChangeText={(name: string) => setName(name)}
        />

        <AppBtn block title="Save" onClick={addAttendanceEntity} />
      </Grid>
    </>
  );
};
export default withRouter(withPageLayout(AttendanceEntityAdd));
