import api from "../index";
import { AttendanceEntityDTO } from "./model.interface";

export const get = async () => await api.get("attendance-entities");
export const details = async (id: number) =>
  await api.get(`attendance-entities/${id}`);
export const post = async (attendanceEntityDTO: AttendanceEntityDTO) =>
  await api.post("attendance-entities", attendanceEntityDTO);

export const put = async (
  id: number,
  attendanceEntityDTO: AttendanceEntityDTO
) => await api.put(`attendance-entities/${id}`, attendanceEntityDTO);
